import { registerBlockType } from '@wordpress/blocks';

import Edit from './edit';
import Save from './save';
import saveDep from './deprecated';
import metadata from './block.json';
import HorizontalAccordionBlockIcon from '../components/icons/block-icons/horizontal-accordion';
import './style.scss';

registerBlockType(metadata, {
	icon: {
		src: HorizontalAccordionBlockIcon,
		foreground: '#3e7fe0',
	},
	edit: Edit,
	save: Save,
	deprecated: [
		{
			save: saveDep
		}
	]
});
