import React from 'react';
import {
	InnerBlocks as DefaultInnerBlocks,
	useBlockProps as useBlockDefaultProps,
} from '@wordpress/block-editor';

export default function Output({ isEditor = false, ...props }) {
	const useBlockProps = isEditor ? useBlockDefaultProps : useBlockDefaultProps.save;
	const InnerBlocks = isEditor ? DefaultInnerBlocks : DefaultInnerBlocks.Content;

	const { uniqueId, activatorEvent } = props.attributes;

	return (
		<>
			<div
				{...useBlockProps({
					className: ``,
					id: `horizontal-accordion-${uniqueId}`,
					'data-activator-event': activatorEvent
				})}
			>
				<InnerBlocks
					allowedBlocks={['aab/horizontal-accordion-item']}
					template={[
						['aab/horizontal-accordion-item'],
						['aab/horizontal-accordion-item'],
						['aab/horizontal-accordion-item'],
					]}
				/>
			</div>
		</>
	);
}
