import React from "react";
import {
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
  PanelBody,
} from '@wordpress/components';
import {__} from '@wordpress/i18n';

export default function ActivatorEventPanel({ attributes, setAttributes }) {
  const { activatorEvent } = attributes;

  return (
    <PanelBody
      title={__('Activator Event', 'advanced-accordion-block')}
      initialOpen
    >
      <ToggleGroupControl
        value={activatorEvent}
        onChange={activatorEvent => setAttributes({ activatorEvent })}
        label={__('Activator Event', 'advanced-accordion-block')}
      >
        <ToggleGroupControlOption
          value="click"
          label="Click"
        />
        <ToggleGroupControlOption
          value="hover"
          label="Hover"
        />
      </ToggleGroupControl>
    </PanelBody>
  )
}