import React from 'react';

import Output from './output';
import Editor from './editor';
import './editor.scss';

const Edit = (props) => {
    const { attributes, clientId } = props;

    return (
        <>
            <Editor.Styles {...props} />
            <Editor.Settings {...props} />
            <Output isEditor {...props} />
        </>
    );
};

export default Edit;